<script>
  import { createEventDispatcher } from "svelte";
  import { scale } from "svelte/transition";

  import Icon from "components/Icon";

  export let c = '';
  export let removable = false;
  export let icon = '';
  export let outlined = false;
  export let selected = false;
  export let selectable = true;

  let value = true;

  $: hoverClass = selected ? "hover:bg-primary-300" : "hover:bg-gray-400";

  const dispatch = createEventDispatcher();

  function close() {
    dispatch("close");
    value = false;
  }

  function select() {
    if (!selectable) return;

    selected = true;
  }
</script>

<style>.p-1\/2 {
  padding: 0.125rem;
}

.outlined {
  background-color: transparent;
  border-width: 1px;
  border-color: #bdbdbd;
  border-style: solid
}

.outlined:hover {
  background-color: #fafafa;
}

.selected {
  border-color: #b027b0;
  color: #b027b0;
  background-color: #e7bee7
}

.selected:hover {
  background-color: #f6e5f6;
}

/*# sourceMappingURL=data:application/json;base64, */</style>

{#if value}
  <span class="{c} mx-1 inline-block" out:scale={{ duration: 100 }}>
    <button
      class="flex items-center rounded-full bg-gray-300 px-2 py-1"
      on:click
      class:outlined
      class:selected
      class:ripple-primary={selectable}
      on:click={select}>
      {#if icon}
        <Icon small c={selected ? 'text-primary-400' : 'text-gray-600'}>
          {icon}
        </Icon>
      {/if}
      <span class="px-2 text-sm">
        <slot />
      </span>
      {#if removable}
        <span
          class="rounded-full p-1/2 inline-flex items-center cursor-pointer {hoverClass}"
          class:bg-gray-500={!selected}
          class:bg-primary-400={selected}
          on:click|stopPropagation={close}>
          <Icon c="text-white" xs>clear</Icon>
        </span>
      {/if}
    </button>
  </span>
{/if}
