<script>
  import { createEventDispatcher } from "svelte";
  import Icon from "components/Icon";

  export let icon = "";
  export let id = "";
  export let value = "";
  export let text = "";
  export let subheading = "";
  export let disabled = false;
  export let dense = false;
  export let navigation = false;
  export let to = "";
  export let selected = false;
  export let basicClasses = "transition p-4 cursor-pointer text-gray-700 flex items-center z-10";

  const dispatch = createEventDispatcher();
</script>

<style>.basic.selected {
  background-color: #eeeeee;
}

.navigation {
  margin-left: 0.75rem;
  margin-right: 0.75rem;
  border-radius: 0.25rem;
  font-size: 0.875rem
}

.navigation.selected {
  background-color: #f6e5f6;
  color: #b027b0;
}

/*# sourceMappingURL=data:application/json;base64, */</style>

<li
  class={basicClasses}
  class:navigation
  class:selected
  class:hover:bg-gray-300={!navigation}
  class:ripple-white={navigation}
  class:ripple-gray={!navigation}
  class:py-2={dense}
  class:text-gray-600={disabled}
  on:click={() => {
    if (disabled) return;
    value = id;
    dispatch('change', id);
  }}
  on:click>
  {#if icon}
    <Icon
      c="pr-6"
      small={dense}
      color={selected && navigation ? 'text-primary-500' : ''}>
      {icon}
    </Icon>
  {/if}

  <div class="flex flex-col p-0">
    <div>
      <slot>{text}</slot>
    </div>
    {#if subheading}
      <div class="text-gray-600 p-0 text-sm">{subheading}</div>
    {/if}
  </div>
</li>
