<script>
  import Icon from "components/Icon";

  export let data = [];
  export let columns = Object.keys(data[0] || {})
    .map(i => ({ label: (i || '').replace('_', ' '), field: i }));

  export let asc = false;
  let sortBy = null;

  $: sorted = sort(sortBy);

  function sort(col) {
    if (!col) return data;

    if (col.sort) return col.sort(data);

    const sorted = data.sort((a, b) => {
      const valA = col.value ? col.value(a) : a[col.field];
      const valB = col.value ?  col.value(b) : b[col.field];

      const first = asc ? valA : valB;
      const second = asc ? valB : valA;

      if (typeof valA === 'number') {
        return first - second;
      }

      return ('' + first).localeCompare(second);
    });

    return sorted;
  }
</script>

<style>table {
  border-radius: 0.25rem;
  box-shadow: 0px 3px 3px -2px rgba(0,0,0,0.20), 0px 3px 4px 0px rgba(0,0,0,0.14), 0px 1px 8px 0px rgba(0,0,0,0.12);
  padding: 0.5rem;
  font-size: 0.875rem
}

table th, table td {
  padding: 0.75rem;
  font-weight: 400;
  text-align: right;
}

table th:first-child, table td:first-child {
  text-align: left;
  border-right-width: 1px;
}

table th {
  color: #757575;
  font-size: 0.75rem;
  cursor: pointer
}

table th .asc {
  -webkit-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  transform: rotate(180deg);
}

table th .sort-wrapper {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

table th:first-child .sort-wrapper {
  justify-content: flex-start;
}

table th .sort {
  width: 1rem;
  height: 1rem;
  opacity: 0;
  transition: .1s;
}

table th:hover {
  color: #000;
  transition: .1s
}

table th:hover .sort {
  opacity: 1;
}

table tr {
  border-color: #eeeeee;
  border-top-width: 1px;
  border-bottom-width: 1px;
  padding-left: 0.75rem;
  padding-right: 0.75rem
}

table tr:hover {
  background-color: #fafafa;
}

/*# sourceMappingURL=data:application/json;base64, */</style>

<table class="p-1">
  <thead class="items-center">
    {#each columns as column, i}
      <slot name="header">
        <th
          class="capitalize"
          on:click={() => {
            if (column.sortable === false) return;
            asc = sortBy === column ? !asc : false;
            sortBy = column;
          }}
        >
          <div class="sort-wrapper">
            {#if column.sortable !== false}
              <span class="sort" class:asc={!asc && sortBy === column}>
                <Icon small color="text-gray-400">arrow_downward</Icon>
              </span>
            {/if}
            <span>{column.label || column.field}</span>
          </div>
        </th>
      </slot>
    {/each}
  </thead>
  <tbody>
  {#each sorted as item}
    <slot name="item">
      <tr>
        {#each columns as column, i}
          <td class={column.class || ''}>
            {#if column.value}
              {@html column.value(item)}
            {:else}
              {item[column.field]}
            {/if}
          </td>
        {/each}
      </tr>
    </slot>
  {/each}
  </tbody>
  <tfoot>
    <slot name="footer" />
  </tfoot>
</table>