<script>
  import { fly } from "svelte/transition";
  import { cubicIn } from "svelte/easing";
  import { Scrim } from "components/Util";

  export let right = false;
  export let persistent = false;
  export let elevation = true;
  export let showMobile = false;
  export let showDesktop = true;
  export let breakpoint = "";
  export let asideClasses = "fixed h-full top-0 md:mt-16 w-auto drawer";
  export let navClasses = `h-full bg-white absolute flex w-auto z-20 drawer
     pointer-events-auto overflow-y-scroll`;

  function shouldShowWithScrim(mobile, persist, bp) {
    if (bp === "sm") {
      return mobile;
    }

    return !persist;
  }

  $: showWithScrim = shouldShowWithScrim(showMobile, persistent, breakpoint);

  $: show = breakpoint === "sm" ? showMobile : showDesktop;

  $: transitionProps = showWithScrim
    ? {
        duration: 200,
        x: right ? 300 : -300,
        opacity: 1,
        easing: cubicIn
      }
    : { x: 0 };

  function hide() {
    if (breakpoint === "sm") {
      showMobile = false;
      return;
    }
    showDesktop = false;
  }
</script>

<style>.drawer {
  min-width: 216px;
}

.bordered {
  border-color: #bdbdbd;
  border-right-width: 1px;
  border-left-width: 1px;
}

/*# sourceMappingURL=data:application/json;base64, */</style>

{#if show}
  <aside
    class={asideClasses}
    class:right-0={right}
    class:left-0={!right}
    class:pointer-events-none={!showWithScrim}
    class:z-50={showWithScrim}
    class:z-20={!showWithScrim}
    on:click={() => (showMobile = false)}>
    {#if showWithScrim}
      <Scrim on:click={hide} />
    {/if}
    <nav
      role="navigation"
      class={navClasses}
      class:elevation-4={elevation}
      class:bordered={!elevation}
      transition:fly={transitionProps}>
      <div class="w-full">
        <slot />
      </div>
    </nav>
  </aside>
{/if}
